* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Montserrat', sans-serif;
	font-size: 14px;
	color: #5d6266;
	background-color: #fff;

	&.no-scroll {
		overflow-y: hidden;
	}
}

img {
	max-width: 100%;
	height: auto;
}

a {
	color: #5d6266;
	text-decoration: none;
	transition: .3s;
}

.container {
	padding: 0px 20px;

	@media (min-width: 768px) {
		padding: 0;
		width: 620px;
		margin: 0 auto;
	}

	@media(min-width: 992px) {
		width: 960px;
	}

	@media (min-width: 1200px) {
		width: 1170px;
	}
}
.section__title {
	font-size: 32px;

	@media (min-width: 768px) {
		font-size: 42px;
	}
}

.brand-color {
	color: #ee0808;
}

.bottom-bg {
	background-color: #0e1b31;
	color: #fff;
	background-image: url(../img/bottom-bg.jpg);
	background-position: center bottom;
	background-repeat: no-repeat;
	background-size: cover;
}

.header {
	color: #fff;
	font-weight: bold;
	background-color: #0a1731;
	background-image: url(../img/header-background.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: right center;
	padding-bottom: 60px;

	@media (max-width: 499px) {
		padding-bottom: 25px;
	}

	&__nav {
		@media (max-width: 1199px) {
			display: flex;
			flex-shrink: 0;
			flex-direction: column;
			justify-content: space-between;
			min-height: 550px;
			height: 100%;


			&-wrapper {
				position: fixed;
				z-index: 2000;
				top: 0;
				right: -320px;
				background-color: #0c172a;
				height: 100vh;
				overflow-y: scroll;
				transition: right .3s;
				max-width: 320px;
				padding: 20px 30px;

				@media (min-width: 1200px) {
					margin-right: auto;
				}

				&.active {
					right: 0;
				}
			}
		}

		

		.fas {
			padding: 10px;
			&:hover {
				cursor: pointer;
			}
		}

		&-title {
			font-weight: bold;
			font-size: 32px;
			position: relative;
			z-index: 5;
			display: flex;
			justify-content: space-between;
			align-items: center;

			@media (min-width: 1200px) {
				display: none;
			}
		}

		&-list {
			display: flex;
			flex-direction: column;

			@media (min-width: 1200px) {
				margin-bottom: 0;
				flex-direction: row;
			}

			a {
				color: #f6f7f8;
				padding: 5px 0;
				&:hover {
					color: rgba(255, 255, 255, .45);
				}
			}
		}

		&-contacts {
			@media (min-width: 1200px) {
				display: none;
			}
		}

		&-subtitle {
			font-weight: bold;
			font-size: 16px;
			margin-bottom: 15px;
		}

		&-phone {
			margin-bottom: 40px;

			a {
				color: #fff;
				display: block;
				margin-bottom: 5px;
			}
		}

		&-social {
			display: flex;

			&-item {
				display: flex;
				width: 30px;
				height: 30px;
				background-color: #364b6e;
				color: #0c172a;
				margin-right: 10px;

				i {
					margin: auto;
					font-size: 18px;
				}
			}
		}

		@media (min-width: 1200px) {
			position: static;
			flex-direction: row;
			margin-right: auto;
			flex-wrap: wrap;

			a {
				color: #f6f7f8;
				padding: 12px;
				position: relative;

				&:hover {
					color: rgba(255, 255, 255, .45);
				}
				
				&.current {
					@media (min-width: 1200px) {
						&:after {
							content: "";
							position: absolute;
							left: 0;
							bottom: -12px;
							display: block;
							height: 2px;
							width: calc(100% - 24px);
							left: 12px;
							background-color: #ee0808;
						}
					}
					
				}
			}
		}
	}

	&__top {
		border-bottom: 1px solid rgba(255, 255, 255, .18);

		.container {
			display: flex;
			align-items: center;
		}
	}

	&__logo {
		margin-right: auto;

		@media (min-width: 1200px) {
			margin-right: 50px;
		}
	}

	&__phone {
		flex-shrink: 0;

		a {
			font-size: 18px;
			font-weight: bold;
			color: #f6f7f8;
			display: block;
			padding: 20px 10px;

			&:hover {
				color: rgba(255, 255, 255, .45);
			}
		}
	}

	&__price {
		display: none;
		position: relative;

		&-btn {
			text-align: left;
			border: none;
			width: 170px;
			background-color: #0fa8f4;
			color: #fff;
			padding: 10px 15px;
			padding-left: 45px;
			box-shadow: 0 0 18px #0ff4ec;
			position: relative;
			margin-left: 15px;
			border-radius: 5px;
			transition: .3s;

			&:hover {
				cursor: pointer;
				background-color: darken(#0fa8f4, 10%);

				.fas {
					background-color: darken(#0b85db, 10%);

					&:after {
						border-left-color: darken(#0b85db, 10%);
					}
				}
			}


		}

		.fas {
			color: #fff;
			position: absolute;
			font-size: 20px;
			width: 30px;
			height: 42px;
			background-color: #0b85db;
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 2;
			left: -4px;
			top: 4px;
			transition: .3s;

			&:after {
				content: "";
				position: absolute;
				top: 0;
				right: -10px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 21px 0 21px 10px;
				border-color: transparent transparent transparent #0b85db;
				transition: .3s;
			}
		}

		@media (min-width: 1200px) {
			display: block;
		}
	}

	&__hamburger {
		width: 60px;
		height: 60px;
		background-color: #0fa8f4;
	}

	&__title {
		margin-bottom: 470px;


		@media (max-width: 499px) {
			font-size: 25px;
			margin-bottom: 250px;
		}

		@media (min-width: 992px) {
			text-align: left;
			padding-left: 50%;
			margin: 150px 0;
			position: relative;
			z-index: 2;
		}

		&-bottom {
			@media (min-width: 1200px) {
				display: block;
				margin-top: 80px;
			}
		}
	}
	&__subtitle {
		display: block;
		position: relative;
		z-index: 5;

		@media (max-width: 499px) {
			font-size: 23px;
		}

		@media (min-width: 992px) {
			text-align: left;
			position: relative;
			z-index: 10;
		}
	}

	&__content {
		padding-top: 55px;
		position: relative;

		@media (max-width: 499px) {
			padding-top: 25px;
		}

		@media (min-width: 768px) {
			padding-bottom: 25px;
		}

		&-bg {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;

			background-image: url(../img/header-banner.png);
			background-repeat: no-repeat;
			background-position: center 35px;
			background-size: cover;

			@media(max-width: 499px) {
				background-position: center 10px;
			}

			@media(min-width: 768px) {
				background-position: center 35px;
				max-width: 768px;
				margin: auto;
			}

			@media(min-width: 992px) {
				max-width: 850px;
				left: -600px;
				background-position: center -110px;
			}

			@media(min-width: 1200px) {
				max-width: 1000px;
				left: -670px;
				background-position: center -160px;
			}

		}
	}

	&__advantages {

		@media (min-width: 1200px) {
			background-size: contain;
			background-position: left center;
		}

		.container {
			padding-top: 50px;
			display: flex;
			flex-wrap: wrap;
		}

		&-item {
			width: calc((100% - 2px) / 2);
			margin-right: 2px;
			margin-bottom: 2px;


			@media (max-width: 499px) {
				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@media (min-width: 500px) and (max-width: 991px) {
				width: calc((100% - 2px * 2) / 3);

				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			@media (min-width: 992px) {
				width: calc((100% - 2px * 5) / 6);
				&:nth-child(6n) {
					margin-right: 0;
				}
			}
		}

		&-wrapper {
			position: relative;
			padding-top: 100%;
			height: 0;
			background-repeat: no-repeat;
			background-position: 20px 20px;
			background-size: auto 60px;
			background-color: #0c172a;
			transition: background-color .3s;

			&:hover {
				background-color: lighten(#0c172a, 15%);
			}


			&:nth-child(1) {
				background-image: url(../img/svg/low-prices.svg);
			}

			&:nth-child(2) {
				background-image: url(../img/svg/quality.svg);
			}

			&:nth-child(3) {
				background-image: url(../img/svg/rf-warehouse.svg);
			}

			&:nth-child(4) {
				background-image: url(../img/svg/delivery.svg);
			}

			&:nth-child(5) {
				background-image: url(../img/svg/discounts.svg);
			}

			&:nth-child(6) {
				background-image: url(../img/svg/fast.svg);
			}
			
		}
		&-content {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			padding: 7%;
			display: flex;
			align-items: flex-end;
			color: #f6f7f8;
			min-height: 36px
		}
	}



	&__nav-icon {
		flex-shrink: 0;
		transition: 1s;

		@media (min-width: 1200px) {
			display: none;
		}
	}

	&__content {
		text-align: center;
	}

}

.ask-price {
	color: #0f1c32;
	padding-top: 30px;
	

	.container {
		@media(min-width: 1200px) {
			display: flex;
			flex-direction: column;
			align-items: flex-end;
		}
	}

	&__order-form {
		position: relative;
		box-shadow: 0px 4px 9px 0px rgba(5, 5, 5, 0.18);
		padding: 25px 30px 30px;

		
		@media (min-width: 1200px) {
			width: 570px;
		}

		h2 {
			margin-bottom: 20px;
		}
	}

	&__catalog-image {
		position: absolute;
		top: 10px;
		right: 10px;
		width: 180px;

		.size {
			display: none;
		}

		img {
			width: 100%;
		}

		.is-sm {
			display: none;
			
			@media (min-width: 500px) {
				display: block;
			}

			@media(min-width: 768px) {
				display: none;
			}
		}

		.is-md {
			@media(min-width: 768px) {
				display: block;
			}
			@media(min-width: 1200px) {
				display: none;
			}
		}

		.is-xl {
			@media(min-width: 1200px) {
				display: block;
			}
		}

		@media (min-width: 768px) {
			top: -50px;
			right: 20px;
			width: 270px;
		}

		@media (min-width: 1200px) {
			width: 650px;
			top: -50px;
			left: -635px;
		}
	}

	&__description {

		@media (min-width: 500px) {
			width: calc(100% - 190px);
		}
		

		@media(min-width: 768px) {
			width: calc(100% - 275px);
		}

		@media(min-width: 1200px) {
			width: auto;
		}
	}

	input {
		width: 100%;
		margin-top: 25px;
		background-color: #f6f7f8;
		border: none;
		line-height: 21px;
		color: #5d6266;
		padding: 15px;
		padding-left: 50px;
		background-repeat: no-repeat;
		background-position: 15px 15px;
		display: inline-block;

		&:focus {
			outline: 1px solid #0fa8f4;
		}

		@media (min-width: 768px) {
			width: calc(50% - 12px);
		}

		&[placeholder="Ваше имя"] {
			background-image: url(../img/user-icon.png);

			@media (min-width: 768px) {
				margin-right: 20px;
			}
		}

		&[placeholder="Ваш email"] {
			background-image: url(../img/mail-icon.png);
		}
	}

	&__button {
		width: 100%;
		background-color: #0fa8f4;
		border: none;
		margin-top: 30px;
		font-size: 16px;
		padding: 15px;
		color: #fff;
		position: relative;
		transition: .3s;

		&:hover {
			cursor: pointer;
			background-color: darken(#0fa8f4, 10%);

			.fas {
				background-color: darken(#0b85db, 10%);

				&:after {
					border-left-color: darken(#0b85db, 10%);
				}
			}
		}

		.fas {
			position: absolute;
			top: 4px;
			left: -4px;
			font-size: 20px;
			padding: 10px 6px;
			background-color: #0b85db;
			transition: .3s;

			&:after {
				content: "";
				position: absolute;
				top: -1px;
				right: -10px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 21px 0 21px 10px;
				border-color: transparent transparent transparent #0b85db;
				transition: .3s;
			}
		}
	}

	

	&__hint {
		padding: 25px 0;
		padding-left: 50px;
		background-image: url(../img/svg/alarm.svg);
		background-repeat: no-repeat;
		background-position: 10px 50%;

		@media (max-width: 499px) {
			padding-left: 42px;
		}

		@media (min-width: 768px) {
			padding-left: 80px;
			background-position: 33px 50%;
		}
		
		@media(min-width: 1200px) {
			width: 50%;
		}
		
	}

}

.catalog {
	color: #0f1c32;
	padding-top: 30px;
	padding-bottom: 30px;

	.container {
		text-align: center;

		h2 {
			margin-bottom: 20px;
		}

		p {
			color: #5d6266;
			margin-bottom: 0;
			line-height: 16px;
			position: relative;
			z-index: 2;
			color: #fff;
		}
	}

	&__slider {
		max-width: 1170px;
		position: relative;
		margin: 0 auto;
		margin-bottom: 45px;
		padding: 0 20px;

		@media (min-width: 1200px) {
			padding: 0;
		}

		&-title {
			padding-top: 20px;
			position: relative;
			margin-bottom: 5px;
			color: #0f1c32;

			&:before, &:after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				height: 1px;
				width: 100%;
				background-color: #f6f7f8;
			}

			&:after {
				width: 70px;
				background-color: #091ddb;
			}
		}

		.swiper-slide {
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		.swiper-button-next,
		.swiper-button-prev {
			background: none;
			font-size: 28px;
			color: #091ddb;
			width: 35px;
			height: 38px;
			top: auto;
			left: auto;
			bottom: -88px;
			right: 200px;
			display: none;
			transition: .3s;

			&:hover {
				color: #9099ef;
			}

			&:focus {
				outline: none;
			}

			.fas {
				padding: 5px;
			}

			@media (min-width: 1200px) {
				display: block;
			}
		}

		.swiper-button-prev {
			right: 250px;
		}
	}

	&__wholesale {

		@media (min-width: 1200px) {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&-price {
			width: 100%;
			background-color: #0fa8f4;
			border: none;
			margin-top: 30px;
			font-size: 16px;
			padding: 15px;
			padding-left: 40px;
			position: relative;

			@media(min-width: 768px) {
				padding: 15px 100px;
			}

			@media(min-width: 1200px) {
				padding-left: 100px;
				padding-right: 100px;
				margin-top: 0;
				width: auto;
				background: linear-gradient(to right, #0fa8f4, #091ddb);
				transition: 1s;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: linear-gradient(to right, darken(#0fa8f4, 10%), darken(#091ddb, 15%));
					opacity: 0;
					transition: 0.3s;
				}

				&:hover {
					&:before {
						opacity: 1;
					}

					.fas {
						background-color: darken(#0b85db, 15%);
						&:after {
							border-left-color: darken(#0b85db, 15%);
						}
					}
				}
			}

			&:hover {
				cursor: pointer;
			}

			.fas {
				position: absolute;
				top: 4px;
				left: -4px;
				font-size: 20px;
				padding: 10px 6px;
				background-color: #0b85db;
				color: #fff;
				transition: .3s;

				&:after {
					content: "";
					position: absolute;
					top: -1px;
					right: -10px;
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 21px 0 21px 10px;
					border-color: transparent transparent transparent #0b85db;
					transition: .3s;
				}
			}
		}

		&-total {
			color: #5d6266;
			display: none;
			width: 170px;
			text-align: left;

			@media (min-width: 1200px) {
				display: block;
			}
		}
	}
}

.why-us {
	color: #0f1c32;
	padding-top: 30px;
	padding-bottom: 650px;
	background-image: url(../img/why-us-bg.jpg);
	background-repeat: no-repeat;
	background-size: 900px;
	background-position: center bottom;

	@media (max-width: 499px) {
		padding-bottom: 600px;
		background-size: 700px;
		background-position: center 90%;
	}



	@media (min-width: 1200px) {
		background-position: center top;
		margin-bottom: 100px;
	}

	.container {
		position: relative;
	}

	h2 {
		text-align: center;
		margin-bottom: 50px;
	}

	&__item {
		padding-left: 60px;
		margin-bottom: 40px;
		position: relative;

		@media (min-width: 1200px) {
			position: absolute;
			margin-bottom: 0;
			display: none;

			&:first-child {
				display: block;
				left: 20px;
				top: 200px;
				width: 470px;
				padding-left: 0;
				padding-right: 100px;

				.fas-wrapper {
					left: auto;
					right: 0;
					margin-top: -10px;
				}
			}

			&:nth-child(2) {
				display: block;
				left: 20px;
				top: 360px;
				width: 375px;
				padding-left: 0;
				padding-right: 100px;

				.fas-wrapper {
					left: auto;
					right: 0;
					margin-top: -10px;
				}
			}

			&:nth-child(3) {
				display: block;
				left: 20px;
				top: 520px;
				width: 405px;
				padding-left: 0;
				padding-right: 100px;

				.fas-wrapper {
					left: auto;
					right: 0;
					margin-top: -10px;
				}
			}

			&:nth-child(4) {
				display: block;
				left: 50%;
				top: 600px;
				transform: translateX(-50%);
				width: 405px;
				padding-left: 0;
				padding-top: 90px;
				text-align: center;

				.fas-wrapper {
					left: 50%;
					transform: translateX(-50%);
					top: 20px;
					margin-top: -10px;
				}
			}

			&:nth-child(5) {
				display: block;
				right: 68px;
				top: 486px;
				width: 360px;
				padding-left: 90px;

				.fas-wrapper {
					left: 0;
					top: 50px;
					margin-top: -10px;
				}
			}

			&:nth-child(6) {
				display: block;
				right: 65px;
				top: 345px;
				width: 330px;
				padding-left: 90px;
				padding-right: 0;

				.fas-wrapper {
					left: 0;
					top: 40px;
					margin-top: -10px;
				}
			}

			&:nth-child(7) {
				display: block;
				right: 15px;
				top: 200px;
				width: 460px;
				padding-left: 90px;
				padding-right: 0;

				.fas-wrapper {
					left: 0;
					top: 40px;
					margin-top: -10px;
				}
			}
		}

		.fas {
			font-size: 9px;
			color: #fff;
			padding: 5px;
			border: 2px solid #fff;
			border-radius: 50%;

			@media (min-width: 1200px) {
				padding: 8px;
				font-size: 13px;
			}

			&-wrapper {
				background-color: #091ddb;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				padding: 10px;
				border-radius: 50%;
				box-shadow: 0px 4px 18px 0px rgba(5, 5, 5, 0.75);

				@media (min-width: 1200px) {
					padding: 14px;
				}
			}
		}

		p {
			line-height: 21px;
		}
	}
}



.warehouse {
	position: relative;

	.container {
		position: relative;
		padding-top: 185px;
		padding-bottom: 55px;

		@media (min-width: 768px) {
			padding-top: 215px;
		}

		@media (min-width: 1200px) {
			padding-top: 60px;
		}
	}

	&__content {
		@media (min-width: 768px) {
			display: flex;
			justify-content: space-between;
		}

		@media (min-width: 1200px) {
			width: calc(50% - 10px);
			justify-content: space-between;
		}
	}

	h2 {
		color: #0f1c32;
		text-align: center;
		position: absolute;
		width: calc(100% - 40px);
		top: -210px;
		left: 50%;
		transform: translateX(-50%);

		@media (min-width: 768px) {
			width: 620px;
		}

		@media (min-width: 1200px) {
			left: 0;
			top: -85px;
			text-align: left;
			transform: none;
		}
	}

	&__contacts {

		@media (min-width: 768px) {
			width: 50%;
			padding-right: 40px;
		}
		@media (min-width: 1200px) {
			padding-right: 30px;
		}
	}

	&__advantages {
		@media (min-width: 768px) {
			width: 50%;
		}
		@media (min-width: 1200px) {
			padding-right: 30px;
		}
	}

	&__map {
		position: absolute;
		top: 0;
		left: 50%;
		width: 100%;
		height: 310px;
		transform: translate(-50%, -50%);
		// background-image: url(../img/warehouse-map.jpg);
		background-size: cover;
		background-position: center;
		
		@media (min-width: 768px) {
			width: 620px;
		}

		@media (min-width: 992px) {
			width: 960px;
		}

		@media (min-width: 1200px) {
			width: 570px;
			left: 100%;
			transform: translateX(calc(-100% - 15px));
			top: -50px;
		}
	}

	&__title {
		font-weight: bold;
		margin-bottom: 22px;
		font-size: 14px;
	}

	&__description {
		color: rgba(255, 255, 255, 0.361);
		line-height: 21px;
		margin: 15px 0 25px;
	}

	&__list {
		list-style: none;

		li {
			padding-left: 30px;
			margin-bottom: 25px;
			position: relative;

			&:before {
				content: "";
				position: absolute;
				width: 12px;
				height: 12px;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				background-color: #ee0808;
				border-radius: 50%;
			}
		}
	}
}

.discounts-and-delivery {
	border-bottom: 1px solid rgba(256, 256, 256, .18);
	padding-bottom: 25px;

	@media (min-width: 768px) {
		padding-bottom: 50px;
	}

	@media (min-width: 1200px) {
		display: flex;
		padding-bottom: 160px;
	}

	.container {
		@media (min-width: 1200px) {
			display: flex;
		}
	}

	.section__title {
		text-align: center;
		margin-bottom: 25px;
	}

	.discounts {

		@media (min-width: 1200px) {
			width: 50%;
			padding: 0 15px;
		}

		&__content {
			border: 10px solid #092759;
			padding: 30px 40px;
			margin-bottom: 25px;
		}


		&__list {
			@media (min-width: 768px) {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
			}
			&-item {
				position: relative;

				@media (min-width: 768px) {
					width: 50%;
					&:first-child {
						padding-right: 50px;
					}
				}

				p {
					margin-bottom: 35px;
					position: relative;
					z-index: 2;

					&:before {
						content: "01";
						position: absolute;
						width: 20px;
						height: 20px;
						left: -30px;
						top: -30px;
						color: #092759;
						font-size: 72px;
						font-weight: bold;
						z-index: -1;
					}
				}

				&:nth-child(2) {
					p {
						&:before {
							content: "02";
						}
					}
				}
			}
		}

		&__details {

			@media (min-width: 768px) {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}

			&-title {
				margin-bottom: 15px;
				padding-right: 20px;
				font-weight: bold;

				@media (min-width: 768px) {
					margin-bottom: 0;
					width: 50%;
					text-align: left;
				}
			}

			&-phone {
				a {
					font-size: 24px;
					font-weight: bold;
					color: #fff;

					&:hover {
						color: rgba(255, 255, 255, .45);
					}
				}

				@media (min-width: 768px) {
					width: 50%;
				}
				
			}
		}
	}

	.delivery {
		background-image: url(../img/map.png);
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;

		@media (min-width: 1200px) {
			width: 50%;
			padding-left: 15px;
		}

		.section__title {
			@media (min-width: 1200px) {
				text-align: left;
			}
		}

		&__content {
			display: flex;
			flex-wrap: wrap;
		}

		&__description {
			margin-bottom: 50px;

			@media (min-width: 768px) {
				width: 480px;
				padding-right: 20px;
			}

			@media (min-width: 1200px) {
				width: 400px;
				padding-right: 30px;
			}
		}

		&__item {
			width: 50%;
			padding-right: 20px;
			margin-bottom: 30px;

			@media (min-width: 768px) {
				width: 140px;
				margin-right: 100px;

				&:nth-of-type(3n+1) {
					margin-right: 0;
				}
			}

			@media (min-width: 992px) {
				margin-right: 0;
			}

			@media (min-width: 1200px) {
				width: 170px;
				margin-right: 30px;
			}
		}

		&__logo {
			margin-bottom: 15px;
		}


	}
}

.footer {
	padding-top: 60px;
	padding-bottom: 55px;

	@media (min-width: 768px) {
		padding-top: 30px;
	}

	@media (min-width: 1200px) {
		padding-top: 60px;
	}

	.container {
		display: flex;
		flex-wrap: wrap;
	}

	&__nav {
		display: none;

		@media (min-width: 1200px) {
			display: block;
			div {
				display: flex;
				list-style: none;
				align-self: center;

				a {
					padding: 10px 12px;
					color: #f6f7f8;
					position: relative;

					&:hover {
						color: rgba(255, 255, 255, .45);
					}

					&.current {
						&:after {
							content: "";
							position: absolute;
							left: 0;
							bottom: -12px;
							display: block;
							height: 2px;
							width: calc(100% - 24px);
							left: 12px;
							background-color: #ee0808;
						}
					}
				}
			}
		}
	}

	&__wholesale {
		flex-grow: 1;
		order: 2;
		position: relative;

		@media (min-width: 768px) {
			order: 3;
			width: calc(50% - 10px);
		}

		@media (min-width: 1200px) {
			order: 2;
			width: 370px;
			flex-grow: 0;
			margin-left: auto;
			margin-bottom: 55px;
		}

		



		&-price {
			display: block;
			width: 100%;
			position: relative;
			font-size: 16px;
			padding: 15px 35px;
			text-align: center;
			background-color: #0fa8f4;
			color: #f6f7f8;
			box-shadow: 0 0 18px rgba(15, 244, 236, .73);
			margin-bottom: 27px;
			border: none;
			transition: .3s;

			&:hover {
				cursor: pointer;
				background-color: darken(#0fa8f4, 10%);

				.fas {
					background-color: darken(#0b85db, 10%);
					&:after {
						border-left-color: darken(#0b85db, 10%);
					}
				}
			}

			.fas {
				color: #fff;
				position: absolute;
				font-size: 20px;
				width: 30px;
				height: 42px;
				background-color: #0b85db;
				display: flex;
				justify-content: center;
				align-items: center;
				z-index: 2;
				left: -4px;
				top: 4px;
				transition: .3s;



				&:after {
					content: "";
					position: absolute;
					top: 0;
					right: -10px;
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 22px 0 22px 10px;
					border-color: transparent transparent transparent #0b85db;
					transition: .3s
				}
			}
		}

	}

	&__contacts {
		width: 100%;
		margin-bottom: 15px;
		display: flex;
		order: 3;

		@media (max-width: 499px) {
			flex-direction: column;
			align-items: center;
		}

		@media (min-width: 768px) {
			flex-direction: column;
			width: 50%;
			padding-right: 20px;
			order: 2;
		}

		@media (min-width: 1200px) {
			width: 300px;
			padding-right: 30px;	
		}

		a {
			width: 50%;
			font-weight: bold;
			font-size: 18px;
			color: #fff;

			&:hover {
				color: rgba(255, 255, 255, .45);
			}

			@media (max-width: 499px) {
				width: auto;
				font-size: 20px;
				padding: 3px;
			}

			@media (min-width: 768px) {
				width: 100%;
				font-size: 24px;
			}

			&.footer__email {
				@media (min-width: 768px) {
					font-size: 14px;
				}
			}	
		}
	}

	&__phone {
		@media (min-width: 768px) {
			margin-bottom: 10px;
		}
	}

	&__info {
		width: 50%;
		font-size: 11px;
		order: 4;

		@media (min-width: 1200px) {
			width: 300px;
			padding-left: 130px;
			margin-right: 200px;
		}

		div {
			margin-bottom: 9px;
		}
	}

	&__requisites {
		font-size: 11px;
		width: 50%;
		order: 5;

		@media (min-width: 1200px) {
			width: 370px;
		}

		div {
			margin-bottom: 9px;
		}
	}
}


.overlay {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .5);
	z-index: 1009;
}

.modal {
	display: none;
	position: fixed;
	z-index: 1010;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	max-width: 570px;
	padding: 30px;
	background-color: #fff;
	color: #0e1d34;

	h2 {
		font-size: 40px;
		line-height: 50px;
		margin-bottom: 15px;
		padding-right: 32px;

		@media (max-width: 499px) {
			font-size: 22px;
			line-height: 25px;
		}
	}

	&__close {
		position: absolute;
		top: 30px;
		right: 30px;
		background: none;
		border: none;
		padding: 10px;

		&:hover {
			cursor: pointer;
		}
		
		.fas {
			font-size: 24px;
			color: #a9aeb2;

			&:hover {
				cursor: pointer;
			}
		}
	}

	p {
		margin-bottom: 15px;
	}

	input {
		width: 100%;
		margin-bottom: 30px;
		background-color: #f6f7f9;
		border: none;
		padding: 15px;
		padding-left: 55px;
		background-repeat: no-repeat;
		background-position: 15px center;

		@media (max-width: 499px) {
			margin-bottom: 7px;
		}

		&:focus {
			outline: 1px solid #0fa8f4;
		}

		&.name {
			background-image: url(../img/user-icon.png);
			
		}

		&.email {
			background-image: url(../img/mail-icon.png);
		}

		&.phone {
			background-image: url(../img/svg/phone-solid.svg);
			background-size: 20px;
		}
	}

	.submit {
		width: 100%;
		padding: 15px;
		border: none;
		background-color: #0fa8f4;
		color: #fff;
		font-size: 16px;
	}
}


#nav-icon {
	position: relative;
	width: 60px;
	height: 60px;
	cursor: pointer;
	
	background-color: #0fa8f4;
	padding: 16px;
	box-shadow: 0 0 18px #0ff4ec;

	span {
		display: block;
		height: 5px;
		background: #ffffff;

		&:nth-child(1) {
			margin-bottom: 6px;
		}

		&:nth-child(2) {
			margin-bottom: 6px;
		}
	}
}